.card {
  background-size: cover;
  background-position: 50%;
  background: #fff;
  border-radius: 2px;
  height: 220px;
  margin: 30px;
  position: relative;
  width: 250px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  cursor: pointer;
}

.card:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  -webkit-box-shadow: 0 14px 28px #999, 0 10px 10px #999;
          box-shadow: 0 14px 28px #999, 0 10px 10px #999;
}

.card > .img-container {
  height: 100%;
}
.card > .img-container > img {
  width: 100%;
  height: 100%;
}




